import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
export default function Navbar(props) {
  return (
    <div>
      <nav
        className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}
      >
        <div className="container-fluid">
          <a className="navbar-brand" href="/">
            {props.title}
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/about">
                  Goto About
                </Link>
              </li>
            </ul>
            <form className="d-flex">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button className="btn btn-outline-success" type="submit">
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>
      <p style={{ color: "blue" }}>
        The above texts is coming from NAVBAR FILE
      </p>
      <div
        className={`form-check form-switch text-${
          props.mode === "dark" ? "light" : "dark"
        }`}
      >
        <input
          className="form-check-input"
          onClick={props.toggleMode}
          type="checkbox"
          id="flexSwitchCheckDefault"
        />
        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">
          Enable Dark Mode
        </label>
      </div>
      <div
        className={`form-check form-switch text-${
          props.mode === "dark" ? "light" : "dark"
        }`}
      >
        <input
          className="form-check-input"
          onClick={props.toggleModeR}
          type="checkbox"
          id="flexSwitchCheckDefaultR"
        />
        <label className="form-check-label" htmlFor="flexSwitchCheckDefaultR">
          Enable Red Mode
        </label>
      </div>
      <div
        className={`form-check form-switch text-${
          props.mode === "dark" ? "light" : "dark"
        }`}
      >
        <input
          className="form-check-input"
          onClick={props.toggleModeG}
          type="checkbox"
          id="flexSwitchCheckDefaultG"
        />
        <label className="form-check-label" htmlFor="flexSwitchCheckDefaultG">
          Enable Green Mode
        </label>
      </div>
      <div
        className={`form-check form-switch text-${
          props.mode === "dark" ? "light" : "dark"
        }`}
      >
        <input
          className="form-check-input"
          onClick={props.toggleModeB}
          type="checkbox"
          id="flexSwitchCheckDefaultB"
        />
        <label className="form-check-label" htmlFor="flexSwitchCheckDefaultB">
          Enable Blue Mode
        </label>
      </div>
    </div>
  );
}

Navbar.propTypes = {
  title: PropTypes.string.isRequired,
};

Navbar.defaultProps = {
  title: "MyDefaultTitle",
};
